import { Directive, Input, SimpleChanges, OnChanges, ElementRef } from '@angular/core';

/**@hidden */
export class SnackbarMethodDescription {
  method: 'show' | 'hide';
  animate: boolean;

  constructor(method: 'show' | 'hide', animate: boolean) {
    this.method = method;
    this.animate = animate;
  }
}

@Directive({ selector: '[showHideSnackbar]' })
export class ShowHideSnackbarDirective implements OnChanges {
  /** @hidden */
  @Input() methodDescription: SnackbarMethodDescription;
  private element: ElementRef;

  constructor(el: ElementRef) {
    this.element = el;
  }
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.methodDescription) {
      if (changes.methodDescription.currentValue?.method === 'show') {
        setTimeout(
          () =>
            this.element.nativeElement?.showSnackbar(
              'relative',
              !changes.methodDescription.currentValue?.animate
            ),
          0
        );
      } else if (changes.methodDescription.currentValue?.method === 'hide') {
        this.element.nativeElement?.hideSnackbar(changes.methodDescription.currentValue?.animate);
      }
    }
  }
}
