import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Output,
  ViewChild
} from '@angular/core';
import { AvlList } from '@avl-controls/core/dist/avl-list/avl-list.api';
import {
  AvlOverflowMenuGroup,
  AvlOverflowMenuItem
} from '@avl-controls/core/dist/avl-overflow-menu/avl-overflow-menu.api';
import { AvlNotification } from '@avl-controls/interfaces';
import { AvlNotificationMenuItem } from '../../models/notification-menu.model';
import { NOTIFICATIONS_OVERFLOW_MENU, TYPE_ICON_MAPPINGS } from './avl-notification-menu.consts';

@Component({
  selector: 'avl-notification-menu',
  templateUrl: './avl-notification-menu.component.html',
  styleUrls: ['./avl-notification-menu.component.css']
})
export class AvlNotificationMenuComponent implements AfterViewInit {
  @Input() open: boolean = false;
  @Input() trigger: HTMLElement;
  @Input() 
  set notifications(notifications: AvlNotification[]) {
    this.notificationList = this.prepareNotifications(notifications);
    this.numberOfUnreadNotifications = notifications.filter(notif => !notif.read).length;
  }

  @Output() openChange = new EventEmitter<boolean>();
  @Output() openDetails = new EventEmitter<void>();
  @Output() notificationStateChanged = new EventEmitter<AvlNotification>();
  @Output() markAllAsRead = new EventEmitter<void>();
  @Output() markAllAsUnread = new EventEmitter<void>();

  @ViewChild('list') list: ElementRef<AvlList>;

  public overflowMenuItems: AvlOverflowMenuGroup[] = [];
  public notificationList: AvlNotificationMenuItem[] = [];
  public numberOfUnreadNotifications: number = 0;

  private overflowMenuHandlers: { [key: string]: () => void } = {
    markAllAsRead: () => {
      this.markAllAsRead.emit();
      this.numberOfUnreadNotifications = 0;
    },
    markAllAsUnread: () => {
      this.markAllAsUnread.emit();
      this.numberOfUnreadNotifications = this.notificationList.length;
    },
    undefined: () => {}
  };

  constructor() {
    this.translateLabels();
  }

  public ngAfterViewInit(): void {
    this.list.nativeElement.revertedList = true;
  }

  public onNotificationStateChanged(event: Event): void {
    const notificationMenuItem: AvlNotificationMenuItem = { ...(<CustomEvent>event).detail.value };
    const notification = {
      ...notificationMenuItem,
      read: notificationMenuItem.iconToggleValue
    } as AvlNotification;
    notification.read ? this.numberOfUnreadNotifications-- : this.numberOfUnreadNotifications++;
    this.notificationStateChanged.emit(notification);

    this.list.nativeElement.removeFocus();
  }

  public onItemClicked(item: AvlOverflowMenuItem): void {
    this.overflowMenuHandlers[item.action]();
  }

  public onOpenChanged(event: Event): void {
    if (!(<CustomEvent>event).detail.value) {
      this.openChange.emit(false);
    } else {
      this.notificationList = [...this.notificationList];
    }
  }

  public onOverflowMenuClicked(): void {
    this.list.nativeElement.removeFocus();
  }

  public onDetailsClicked(): void {
    this.open = false;
    this.openDetails.emit();
    this.openChange.emit(false);
  }

  private prepareNotifications(notifications: AvlNotification[]): AvlNotificationMenuItem[] {
    return notifications.map(notification => ({
      ...notification,
      label: notification.messageText,
      sublabel: this.formatDateTime(notification.timestamp),
      icon: TYPE_ICON_MAPPINGS[notification.type],
      iconToggleUnchecked: 'mail',
      iconToggleChecked: 'communication:mail-outline',
      iconToggleValue: notification.read
    }));
  }

  private formatDateTime(date: Date): string {
    return date === null
      ? ''
      : `${AvlLocalizationService.formatDateTime(date, {
          format: 'date-short'
        })} ${AvlLocalizationService.formatDateTime(date, { format: 'time-long' })}`;
  }

  private async translateLabels() {
    if (!AvlLocalizationService || !AvlLocalizationService.translationService) {
      return;
    }

    this.overflowMenuItems = await this.translateMenu(NOTIFICATIONS_OVERFLOW_MENU);
  }

  private async translateMenu(menu: AvlOverflowMenuGroup[]): Promise<AvlOverflowMenuGroup[]> {
    return await Promise.all(menu.map(async group => await this.translateMenuGroup(group)));
  }

  private async translateMenuGroup(group: AvlOverflowMenuGroup): Promise<AvlOverflowMenuGroup> {
    return {
      items: await Promise.all(group.items.map(async item => await this.translateMenuItem(item)))
    };
  }

  private async translateMenuItem(item: AvlOverflowMenuItem): Promise<AvlOverflowMenuItem> {
    const translateService = AvlLocalizationService.translationService;
    return {
      ...item,
      label: await translateService.translate(item.label),
      sublabel: item.sublabel ? await translateService.translate(item.sublabel) : null
    };
  }
}
