import { Directive, HostListener, Output, EventEmitter } from '@angular/core';

@Directive({ selector: '[snackbarListener]' })
export class SnackbarListener {
  /** @hidden */
  @Output() notificationClicked: EventEmitter<Event> = new EventEmitter();
  /** @hidden */
  @Output() buttonClicked: EventEmitter<Event> = new EventEmitter();
  /** @hidden */
  @Output() notificationDismissed: EventEmitter<Event> = new EventEmitter();
  @HostListener('notification-clicked', ['$event']) onNotificationClicked(event: Event) {
    this.notificationClicked.emit(event);
  }
  @HostListener('button-clicked', ['$event']) onButtonClicked(event: Event) {
    if (event instanceof Event) {
        this.buttonClicked.emit(event);
    }
  }
  @HostListener('notification-dismissed', ['$event']) onNotificationDismissed(event: Event) {
    if (event instanceof Event) {
        this.notificationDismissed.emit(event);
    }
  }
}
