import {
  Component,
  Input,
  ViewChild,
  ElementRef,
  Output,
  EventEmitter,
  OnInit,
  ChangeDetectorRef,
  AfterViewChecked
} from '@angular/core';
import { Column } from '@avl-controls/datatable/dist/avl-datatable/column-definitions';
import {
  AvlDatatable,
  PageSizeOption
} from '@avl-controls/datatable/dist/avl-datatable/avl-datatable';

import { AvlNotification } from '@avl-controls/interfaces';

import '@avl-controls/datatable/dist/avl-datatable/avl-datatable';
import '@avl-controls/core/dist/avl-icon-button/avl-icon-button';
import '@avl-controls/core/dist/avl-icon/avl-icon';
import '@avl-controls/core/dist/avl-side-sheet/avl-side-sheet';
import '@avl-controls/core/dist/avl-checkbox/avl-checkbox';
import '@avl-controls/core/dist/avl-tooltip/avl-tooltip';
import '@avl-controls/core/dist/avl-date/avl-date';
import { AvlNotificationTableItem } from '../../models/notification-dialog.model';
import {
  DEFAULT_COLUMN_DEFINITIONS,
  DEFAULT_FILTERS,
  DEFAULT_PAGE_SIZE_OPTIONS,
  TYPE_ICON_STYLE_MAPPINGS,
  TYPE_ICON_MAPPINGS,
  STATE_ICON_MAPPINGS,
  NOTIFICATION_TYPE_MAPPINGS,
  DEFAULT_ICON_STYLE
} from './avl-notification-dialog.consts';
import { NgForm } from '@angular/forms';
@Component({
  selector: 'avl-notification-dialog',
  templateUrl: './avl-notification-dialog.component.html',
  styleUrls: ['./avl-notification-dialog.component.css']
})
export class AvlNotificationDialogComponent implements OnInit, AfterViewChecked {
  @Input() open: boolean = false;
  @Input() pageSizeOptions: PageSizeOption[] = DEFAULT_PAGE_SIZE_OPTIONS;
  @Input() columnDefinitions: Column[] = DEFAULT_COLUMN_DEFINITIONS;
  @Input()
  get notifications(): AvlNotification[] {
    return this._notifications;
  }

  set notifications(notifications: AvlNotification[]) {
    if (this._notifications.length === notifications.length) {
      return;
    }

    this.showRefreshButton = true;
    this._notifications = notifications;
  }

  @Output() openChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() markSelectedAsRead: EventEmitter<any[]> = new EventEmitter<AvlNotification[]>();
  @Output() markSelectedAsUnread: EventEmitter<any[]> = new EventEmitter<AvlNotification[]>();

  @ViewChild('notificationTable') public table: ElementRef<AvlDatatable>;
  @ViewChild('filterForm', { read: NgForm }) filterForm: NgForm;

  public tableData: AvlNotificationTableItem[] = [];
  public filteredData: AvlNotificationTableItem[] = [];
  public badge: boolean = false;
  public disableMarkingSelected: boolean = true;
  public filters = this.deepCopyObject(DEFAULT_FILTERS);
  public filtersOpen = true;
  public showRefreshButton = false;
  public dateFromLabel = 'Date from';
  public dateToLabel = 'Date to';

  private dateFilterGroupInitHeader = 'Date';
  public dateFilterGroupHeader = this.dateFilterGroupInitHeader;

  //notification datatable options
  public selectedRows: AvlNotificationTableItem[] = [];
  public pageSize = 15;

  public appliedFilters = this.deepCopyObject(DEFAULT_FILTERS);

  private _notifications: AvlNotification[] = [];

  constructor(private cdRef: ChangeDetectorRef) {}

  public ngOnInit(): void {
    this.translateLabels();
    this.refreshTable();
  }

  public ngAfterViewChecked(): void {
    this.cdRef.detectChanges();
  }

  /** Emits event when dialog opens or closes */
  public openChanged(open: boolean): void {
    this.openChange.emit(open);
  }

  /** Toggles displaying of filter sheet */
  public toggleFilters(): void {
    this.filtersOpen = !this.filtersOpen;
  }

  /** Refreshes the datatable when refresh button is clicked */
  public refreshTable(): void {
    this.showRefreshButton = false;
    this.tableData = this.prepareNotifications(this._notifications);
    this.filters = this.deepCopyObject(this.appliedFilters);
    this.filterData();
  }

  /**
   * Invoked when clicking apply button - filters data by given filters.
   */
  public applyFilters(): void {
    this.appliedFilters = this.deepCopyObject(this.filters);
    this.filterData();
  }

  /** Filters notifications according to applied filters */
  public filterData(): void {
    this.filteredData = [];
    const filters = this.getActiveFilters();
    for (const element of this.tableData) {
      if (this.doesElementPassEveryFilter(filters, element)) {
        this.filteredData.push(element);
      }
    }
  }

  /** Changes selected notification state to read */
  public markAsRead(): void {
    this.updateStateOfSelectedRowsTo(true);
    this.markSelectedAsRead.emit(this.selectedRows);
  }

  /** Changes selected notification state to unread */
  public markAsUnread(): void {
    this.updateStateOfSelectedRowsTo(false);
    this.markSelectedAsUnread.emit(this.selectedRows);
  }

  /**
   * Resets all filters.
   */
  public reset(): void {
    this.filters = this.deepCopyObject(DEFAULT_FILTERS);
    this.appliedFilters = this.deepCopyObject(DEFAULT_FILTERS);
    this.filterData();
    this.updateLabels();
    this.badge = false;
  }

  /** Calculates active filters from filter object */
  public getActiveFilters(): Object {
    this.badge = false;
    const setFilters = {};
    // Mapping applied filters object to better format used for filtering, 
    // creates arrays from objects
    for (const item of Object.keys(this.appliedFilters)) {
      const secondLevelFilter = [];
      for (const filter of Object.keys(this.appliedFilters[item])) {
        if (this.appliedFilters[item][filter] === true) {
          secondLevelFilter.push(filter);
          this.badge = true;
        }
      }

      if (secondLevelFilter.length) {
        setFilters[item] = secondLevelFilter;
      }

      if (item === 'date') {
        setFilters[item] = [
          this.appliedFilters[item]['dateFromValue'],
          this.appliedFilters[item]['dateToValue']
        ];
      }
    }
    return setFilters;
  }

  /** Called when selection is applied to the table rows */
  public selectedValuesChanged() {
    this.disableMarkingSelected = false;
    if (this.selectedRows.length == 0) {
      this.disableMarkingSelected = true;
    }
  }

  /** Updates date filter header labels when date changes */
  public async updateLabels(): Promise<void> {
    const translationService = AvlLocalizationService.translationService;
    const { dateFromValue, dateToValue } = this.filters.date;
    if (!dateFromValue && !dateToValue) {
      this.dateFilterGroupHeader = this.dateFilterGroupInitHeader;
    } else {
      this.dateFilterGroupHeader = await translationService.translate(
        'VizBox.NotificationDialog.DateRange',
        {
          dateFrom: this.formatDateTime(dateFromValue),
          dateTo: this.formatDateTime(dateToValue)
        }
      );
    }
  }

  /** Calculates number of notifications with specific type */
  public getNumberOfNotificationsWithType(data: AvlNotificationTableItem[], type: string): number {
    return data.filter(element => NOTIFICATION_TYPE_MAPPINGS[element.type] === type).length;
  }

  /** Calculates number of unread notifications */
  public getNumberOfUnreadNotifications(data: AvlNotificationTableItem[]): number {
    return data.filter(element => !element.state).length;
  }

  /** Calculates number of notifications currently displayed in datatable */
  public getNumberOfNotifications(): string {
    if (this.filteredData.length === this.tableData.length && !this.badge) {
      return this.filteredData.length.toString();
    } else {
      return `${this.filteredData.length}/${this.tableData.length}`;
    }
  }

  /** Opens link in new tab when table row is clicked */
  public onRowClicked(event: Event): void {
    const row = (<CustomEvent>event).detail;
    if (row.link) {
      window.open(row.link, '_blank');
    }
  }

  /** Maps general avl notification list to table specific list */
  private prepareNotifications(notifications: AvlNotification[]): AvlNotificationTableItem[] {
    return notifications.map(notification => ({
      ...notification,
      icon: TYPE_ICON_MAPPINGS[notification.type],
      iconStyle: TYPE_ICON_STYLE_MAPPINGS[notification.type],
      currentIconToggle: STATE_ICON_MAPPINGS[String(notification.read)],
      currentIconToggleStyle: DEFAULT_ICON_STYLE,
      state: notification.read
    }));
  }

  /** Formats date time to date-short */
  private formatDateTime(d: Date): string {
    return d ? AvlLocalizationService.formatDateTime(d, { format: 'date-short' }) : '(-)';
  }

  /** Deep copy of filters object */
  private deepCopyObject(object: any): any {
    const copiedObject = {};
    Object.keys(object).forEach(key => {
      copiedObject[key] = Object.assign({}, object[key]);
    });
    return copiedObject;
  }

  /** Determines if element satisfies filter for every key in this.apppliedFilters */
  private doesElementPassEveryFilter(activeFilters: any, element: any): boolean {
    return Object.keys(activeFilters).every(key => {
      if (key === 'date') {
        const [dateFrom, dateTo] = this.prepareDates(activeFilters[key]);

        // If dates not set, filter this element as well
        if (!dateFrom.getTime() && !dateTo.getTime()) {
          return true;
        }

        this.badge = true;

        // If timestamp is not a Date, don't filter this element
        const timestamp = new Date(element.timestamp);
        if (!timestamp.getTime()) {
          return false;
        }
        return this.compareDates(timestamp, dateFrom, dateTo);
      }

      return activeFilters[key].includes(NOTIFICATION_TYPE_MAPPINGS[element[key]]);
    });
  }

  private prepareDates(dates: string[]): [Date, Date] {
    const dateFrom = new Date(dates[0]);
    const dateTo = new Date(dates[1]);
    dateFrom.setHours(0, 0, 0, 0);
    dateTo.setHours(23, 59, 59, 999);
    return [dateFrom, dateTo];
  }

  private compareDates(timestamp: Date, from: Date, to: Date): boolean {
    const ts = timestamp.getTime();
    const fromTs = from?.getTime();
    const toTs = to?.getTime();

    if (fromTs && toTs) {
      return ts >= fromTs && ts <= toTs;
    } else if (fromTs) {
      return ts >= fromTs;
    } else if (toTs) {
      return ts <= toTs;
    }

    return false;
  }

  private updateStateOfSelectedRowsTo(state: boolean): void {
    this.tableData
      .filter(notification => this.isRowSelected(notification.id))
      .forEach(notification => {
        notification.state = state;
        notification.currentIconToggle = STATE_ICON_MAPPINGS[String(state)];
      });
    this.tableData = [...this.tableData];
    this.filterData();
  }

  private isRowSelected(id: number): boolean {
    return this.selectedRows.map(row => row.id).includes(id);
  }

  // Translations
  private async translateLabels() {
    if (!AvlLocalizationService || !AvlLocalizationService.translationService) {
      return;
    }

    const translationService = AvlLocalizationService.translationService;
    this.dateFilterGroupInitHeader = await translationService.translate(
      'VizBox.NotificationDialog.Date'
    );
    this.dateFilterGroupHeader = this.dateFilterGroupInitHeader;
    this.dateFromLabel = await translationService.translate('VizBox.NotificationDialog.DateFrom');
    this.dateToLabel = await translationService.translate('VizBox.NotificationDialog.DateTo');
    this.pageSizeOptions = await this.translatePageSizeOptions(this.pageSizeOptions);
    this.columnDefinitions = await this.translateColumns(this.columnDefinitions);
  }

  private async translatePageSizeOptions(options: PageSizeOption[]): Promise<PageSizeOption[]> {
    return await Promise.all(
      options.map(async option => await this.translatePageSizeOption(option))
    );
  }

  private async translatePageSizeOption(item: PageSizeOption): Promise<PageSizeOption> {
    const translationService = AvlLocalizationService.translationService;
    return {
      ...item,
      label:
        item.value === Number.MAX_SAFE_INTEGER
          ? await translationService.translate('VizBox.NotificationDialog.ShowAll')
          : await translationService.translate('VizBox.NotificationDialog.ShowXPerPage', {
              rows: item.value
            })
    };
  }

  private async translateColumns(column: Column[]): Promise<Column[]> {
    return await Promise.all(column.map(async item => await this.translateColumnItem(item)));
  }

  private async translateColumnItem(item: Column): Promise<Column> {
    const translateService = AvlLocalizationService.translationService;
    return {
      ...item,
      caption: await translateService.translate(item.caption)
    };
  }
}
