import { PageSizeOption } from '@avl-controls/datatable/dist/avl-datatable/avl-datatable';
import {
  Column,
  ColumnType,
  LeafColumn
} from '@avl-controls/datatable/dist/avl-datatable/column-definitions';
import { AvlNotificationType } from '@avl-controls/interfaces';

export const DEFAULT_PAGE_SIZE_OPTIONS: PageSizeOption[] = [
  { value: 15, label: 'Show 15 per page' },
  { value: 50, label: 'Show 50 per page' },
  { value: Number.MAX_SAFE_INTEGER, label: 'Show all' }
];

export const DEFAULT_FILTERS = {
  type: {
    warning: undefined,
    error: undefined,
    info: undefined,
    ok: undefined
  },
  state: {
    read: undefined,
    unread: undefined
  },
  date: {
    dateFromValue: undefined,
    dateToValue: undefined
  }
};

export const DEFAULT_COLUMN_DEFINITIONS = [
  new LeafColumn('VizBox.NotificationDialog.Type', 'icon', ColumnType.Icon, false, false, 0.5),
  new LeafColumn('VizBox.NotificationDialog.Module', 'module', ColumnType.Text, true, false, 3),
  new LeafColumn(
    'VizBox.NotificationDialog.Message',
    'messageText',
    ColumnType.Text,
    true,
    false,
    8
  ),
  new LeafColumn('VizBox.NotificationDialog.Id', 'id', ColumnType.Number, false, false, 1),
  <Column>{
    caption: 'VizBox.NotificationDialog.Timestamp',
    valueField: 'timestamp',
    type: ColumnType.Date,
    sortable: false,
    filterable: false,
    flexWidth: 3,
    dateTimeOptions: { format: 'date-time-short' }
  },
  new LeafColumn(
    'VizBox.NotificationDialog.State',
    'currentIconToggle',
    ColumnType.Icon,
    false,
    false,
    1
  )
];

export const NOTIFICATION_TYPE_MAPPINGS = {
  [0]: 'error',
  [1]: 'warning',
  [2]: 'info',
  [3]: 'ok',
  false: 'unread',
  true: 'read'
};

export const TYPE_ICON_MAPPINGS = {
  [AvlNotificationType.Error]: 'cancel',
  [AvlNotificationType.Warning]: 'warning',
  [AvlNotificationType.Info]: 'info-outline',
  [AvlNotificationType.OK]: 'check'
};

export const STATE_ICON_MAPPINGS = {
  false: 'mail',
  true: 'communication:mail-outline'
};

export const DEFAULT_ICON_STYLE = 'height:24px; width:24px;';

export const TYPE_ICON_STYLE_MAPPINGS = {
  [AvlNotificationType.Error]: `--avl-icon-custom-color: var(--avl-error-color); ${DEFAULT_ICON_STYLE}`,
  [AvlNotificationType.Warning]: `--avl-icon-custom-color: var(--avl-warning-color); ${DEFAULT_ICON_STYLE}`,
  [AvlNotificationType.Info]: `--avl-icon-custom-color: var(--avl-icon-default-color); ${DEFAULT_ICON_STYLE}`,
  [AvlNotificationType.OK]: `--avl-icon-custom-color: var(--avl-ok-color); ${DEFAULT_ICON_STYLE}`
};

