import { Directive, ElementRef, Input, OnChanges, SimpleChanges } from '@angular/core';
import {
  animate,
  AnimationBuilder,
  AnimationMetadata,
  AnimationPlayer,
  AnimationStyleMetadata,
  style
} from '@angular/animations';
import { AvlNotification } from '@avl-controls/interfaces';
import { ANIMATION_DURATION, MARGIN } from '../avl-notification-container.consts';
import { SnackbarMethodDescription } from './show-hide-snackbar.directive';
/** @hidden */
export const enum PositionAnimation {
  ToLowerStack,
  ToTop,
  ToBottom,
  ToStack,
  FromStack
}
@Directive({ selector: '[animateStack]' })
export class AnimateStack implements OnChanges {
  /** @hidden */
  @Input() readonly animateStack: PositionAnimation;

  /** @hidden */
  @Input() readonly notification: AvlNotification;

  /** @hidden */
  @Input() readonly previousStackHeight: number;

  /** @hidden */
  @Input() readonly snackbarMethodDescription: SnackbarMethodDescription;

  /** @hidden */
  @Input() readonly reopen: boolean;

  private styleTop: number;
  private element: ElementRef;
  private player: AnimationPlayer;

  constructor(readonly builder: AnimationBuilder, el: ElementRef) {
    this.element = el;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.animateStack) {
      this.move(this.previousStackHeight);
    } else if (changes.previousStackHeight) {
      this.move(changes?.previousStackHeight.currentValue);
    } else if (changes.snackbarMethodDescription) {
      if (changes.snackbarMethodDescription.currentValue?.method === 'show') {
        this.move(this.previousStackHeight);
      }
    }
  }

  private show(metadata: AnimationMetadata[]) {
    if (this.player) {
      this.player.destroy();
    }
    if (metadata) {
      const factory = this.builder.build(metadata);
      const player = factory.create(this.element.nativeElement);
      player.play();
    }
  }
  private move(height: number) {
    if (this.animateStack === PositionAnimation.ToBottom) {
      this.styleTop = height + MARGIN * 2;
    }
    const animationMetadata: AnimationMetadata[] = this.animate();
    this.show(animationMetadata);
  }
  private animate(): AnimationMetadata[] {
    const initialStyle = style({
      top: '0',
      width: '100%',
      left: '0',
      zIndex: 1
    });
    return this.getAnimation(this.animateStack, initialStyle);
  }

  private getAnimation = (
    animationName: PositionAnimation,
    initialStyle: AnimationStyleMetadata
  ): AnimationMetadata[] => {
    const allStyle = style('*');

    switch (animationName) {
      case PositionAnimation.ToBottom: {
        return [
          allStyle,
          animate(
            ANIMATION_DURATION + 'ms ease-in',
            style({
              top: this.styleTop + 'px',
              zIndex: 0
            })
          )
        ];
      }
      case PositionAnimation.ToTop: {
        return [allStyle, animate(ANIMATION_DURATION + 'ms ease-in', initialStyle)];
      }
      default:
        return [initialStyle, animate('0ms', initialStyle)];
    }
  };
}
