import { AvlNotification } from '@avl-controls/interfaces';
import { BehaviorSubject } from 'rxjs';
import { skipWhile } from 'rxjs/operators';
import { PositionAnimation } from './directives/animate-stack.directive';
import { ANIMATION_DURATION, ACTION_BUTTON_MESSAGE } from './avl-notification-container.consts';
import { SnackbarMethodDescription } from './directives/show-hide-snackbar.directive';

/** @hidden */
export class SnackbarStack {
  /** @hidden */
  public buttonLabel: string;

  /** @hidden */
  public notificationList: AvlNotification[] = [];

  /** @hidden */
  public activeNotification$: BehaviorSubject<AvlNotification> = new BehaviorSubject(undefined);

  /** @hidden */
  public stackAnimation: PositionAnimation;

  /** @hidden */
  public snackbarAnimation: PositionAnimation;

  /** @hidden */
  public stackIndicator = false;

  /** @hidden */
  public stackIndicatorButtonLabel: string;

  /** @hidden */
  public previousNotification: AvlNotification;

  /** @hidden */
  public methodDescription: SnackbarMethodDescription;
  
  /** @hidden */
  public reopen: boolean;
  
  /** @hidden */
  public dismissedSilently: boolean;

  /** @hidden */
  public actionButtonMessage = ACTION_BUTTON_MESSAGE;

  constructor(stackType: string) {
    this.translateLabels();
    this.methodDescription = new SnackbarMethodDescription(undefined, undefined)
    this.activeNotification$
      .pipe(skipWhile(notification => notification === undefined))
      .subscribe((notification: AvlNotification) => {
        if (notification) {
          if (this.notificationList.length > 0) {
            const timeout = setTimeout(
              () => {
                this.openNextNotification(stackType);
                clearTimeout(timeout);
              },
              this.previousNotification ? ANIMATION_DURATION : 0
            );
          } else {
            this.openNextNotification(stackType);
          }
        } else {
          this.resetValues();
        }
      });
  }

  private async translateLabels() {
    if (!AvlLocalizationService || !AvlLocalizationService.translationService) {
      return;
    }

    const translationService = AvlLocalizationService.translationService;
    this.actionButtonMessage = await translationService.translate('VizBox.NotificationContainer._X_more_TYPE_notifications');
  }

  private openNextNotification = (stackType): void => {
    this.methodDescription = new SnackbarMethodDescription('show', !this.reopen);
    if (!this.reopen) {
      this.stackAnimation = PositionAnimation.ToTop;
    } else {
      this.stackAnimation = PositionAnimation.FromStack;
    }
    this.setStackIndicatorProperties(stackType);
    this.reopen = false;
    this.dismissedSilently = false;
  };

  private resetValues = () => {
    this.notificationList = [];
    this.previousNotification = undefined;
    this.stackIndicator = undefined;
  };
  private setStackIndicatorProperties = (stackType: string): void => {
    const notificationsCount = this.notificationList.length;
    this.stackIndicator = notificationsCount > 1;
    const timeout = setTimeout(
      () => {
        this.previousNotification = this.notificationList[notificationsCount-1];
        this.buttonLabel = this.setButtonLabel(notificationsCount, stackType);
        this.stackIndicatorButtonLabel = this.setButtonLabel(notificationsCount - 1, stackType);
        clearTimeout(timeout);
      },
      this.previousNotification ? ANIMATION_DURATION : 0
    );
  };
  private setButtonLabel = (notificationsCount: number, type: string): string => {
    return notificationsCount > 0
      ? this.actionButtonMessage
        .replace('_TYPE ', type === 'info' ? '' : type + ' ')
        .replace('_X', String(notificationsCount))
      : null;
  };
}
