import { Directive, ElementRef, HostBinding, Input, OnChanges, SimpleChanges } from '@angular/core';
import {
  animate,
  AnimationBuilder,
  AnimationMetadata,
  AnimationPlayer,
  AnimationStyleMetadata,
  style
} from '@angular/animations';
import { AvlNotification } from '@avl-controls/interfaces';
import { PositionAnimation } from './animate-stack.directive';
import { ANIMATION_DURATION, MARGIN } from '../avl-notification-container.consts';
/** @hidden */

@Directive({ selector: '[animateSnackbar]' })
export class AnimateSnackbar implements OnChanges {
  /** @hidden */
  @Input() readonly animateSnackbar: PositionAnimation;

  /** @hidden */
  @Input() readonly positionAnimation: PositionAnimation;

  /** @hidden */
  @Input('notification') readonly notification: AvlNotification;

  /** @hidden */
  @Input('reopen') readonly reopen: boolean;

  /** @hidden */
  @HostBinding('class.in-stack') inStack: boolean;

  /** @hidden */
  @HostBinding('class.no-animation') noAnimation: boolean;

  /** @hidden */
  @HostBinding('class.snackbar-container') defaultClass = true;

  private element: ElementRef;
  private player: AnimationPlayer;

  constructor(readonly builder: AnimationBuilder, el: ElementRef) {
    this.element = el;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.animateSnackbar) {
      const isInStack = changes.animateSnackbar.currentValue === PositionAnimation.ToStack;
      if (isInStack) {
        this.move();
      } else if (changes.animateSnackbar.previousValue === PositionAnimation.ToStack) {
        this.move();
      }
    }
  }

  private show(metadata: AnimationMetadata[]) {
    if (this.player) {
      this.player.destroy();
    }
    if (metadata) {
      const factory = this.builder.build(metadata);
      const player = factory.create(this.element.nativeElement);
      player.play();
    }
  }
  private move() {
    const animationMetadata: AnimationMetadata[] = this.animate();
    this.show(animationMetadata);
  }
  private animate(): AnimationMetadata[] {
    const initialStyle = style({
      marginTop: '0',
      width: '100%',
      marginLeft: '0',
      marginRight: '0'
    });
    return this.getAnimation(this.animateSnackbar, initialStyle);
  }

  private getAnimation = (
    animationName: PositionAnimation,
    initialStyle: AnimationStyleMetadata
  ): AnimationMetadata[] => {
    const elementWidth = this.element.nativeElement.offsetWidth - 3 * MARGIN;
    const inStackStyle = style({
      marginTop: MARGIN + 'px',
      marginLeft: MARGIN + 'px',
      marginRight: MARGIN + 'px',
      width: elementWidth + 'px'
    });
    switch (animationName) {
      case PositionAnimation.FromStack:
        return [animate(ANIMATION_DURATION + 'ms ease-in', inStackStyle), initialStyle];

      case PositionAnimation.ToStack: {
        return [initialStyle, animate(ANIMATION_DURATION + 'ms ease-in', inStackStyle)];
      }
      default:
        return [initialStyle, animate('1ms ease-in', initialStyle)];
    }
  };
}
