import {
  AfterViewChecked,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import { Observable } from 'rxjs';
import '@avl-controls/core/dist/avl-snackbar/avl-snackbar';
import { AvlNotification, AvlNotificationType } from '@avl-controls/interfaces';
import { takeUntil } from 'rxjs/operators';
import { SnackbarMethodDescription } from './directives/show-hide-snackbar.directive';
import { Subject } from 'rxjs';
import { PositionAnimation } from './directives/animate-stack.directive';
import { SnackbarStack } from './snackbar-stack';
import { ANIMATION_DURATION } from './avl-notification-container.consts';

const NOTIFICATION_TYPE = new Map<number, string>([
  [AvlNotificationType.Error, 'Error'],
  [AvlNotificationType.Warning, 'Warning'],
  [AvlNotificationType.Info, 'Info'],
  [AvlNotificationType.OK, 'OK']
]);
@Component({
  selector: 'avl-notification-container',
  templateUrl: './avl-notification-container.component.html',
  styleUrls: ['./avl-notification-container.component.css']
})
export class AvlNotificationContainerComponent implements OnInit, OnDestroy, AfterViewChecked {
  /** @hidden */
  @HostBinding('style.top') positionTop: string;

  /** @hidden */
  @ViewChild('warningStackElement') warningStackElement: ElementRef;

  /** @hidden */
  @ViewChild('infoStackElement') infoStackElement: ElementRef;
 
  /** @hidden */
  @ViewChild('infoStackIndicatorElement') infoStackIndicatorElement: ElementRef;
  
  /** @hidden */
  @ViewChild('infoStackIndicatorElement') warningStackIndicatorElement: ElementRef;

  /** If set optional icon (for info and OK notifications) is displayed, hidden otherwise. */
  @Input('display-icon') public displayIcon: boolean;

  /** Observable emits notifications. Default observable emits dummy notifications every 3s for 100s. */
  @Input('notification$') public notification$: Observable<AvlNotification> = new Observable(
    function subscribe(subscriber) {
      let i = 0;
      const intervalId = setInterval(() => {
        const warning = Math.round(Math.random());
        subscriber.next({
          id: i++,
          module: 'notification-container',
          timestamp: new Date(),
          type: warning ? AvlNotificationType.Warning : AvlNotificationType.Info,
          messageText: 'Snackbar with notification' + new Date().toTimeString(),
          read: false,
          link: 'https://vizbox.avl.com'
        });
      }, 3000);

      setTimeout(() => {
        clearInterval(intervalId);
      }, 100000);
    }
  );

  @Output() openMenu: EventEmitter<void> = new EventEmitter<void>();

  /** @hidden */
  public warningStack: SnackbarStack;
  /** @hidden */
  public infoStack: SnackbarStack;

  /** @hidden */
  public previousNotification: AvlNotification;

  /** @hidden */
  public stackHeight: { [key: string]: number } = {};

  /** @hidden */
  public reopen: boolean;

  /** @hidden */
  public notificationList: AvlNotification[] = [];

  private activeNotification: AvlNotification;
  private destroyed$: Subject<void> = new Subject();

  constructor(readonly cdRef: ChangeDetectorRef) {
    this.warningStack = new SnackbarStack(
      String(NOTIFICATION_TYPE.get(AvlNotificationType.Warning)).toLowerCase()
    );

    this.infoStack = new SnackbarStack(
      String(NOTIFICATION_TYPE.get(AvlNotificationType.Info)).toLowerCase()
    );
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

  ngAfterViewChecked(): void {
    this.cdRef.detectChanges();
  }

  /** @hidden */
  public ngOnInit(): void {
    this.positionTop = document.querySelector('main')?.offsetTop + 12 + 'px';
    this.notification$?.pipe(takeUntil(this.destroyed$)).subscribe(notification => {
      this.handleNotification(notification);
    });
  }

  /** @hidden */
  public onButtonClicked = (event: any): void => {
    if (event) {
      this.openMenu.emit();
      this.dismissAll();
    }
  };

  /** @hidden */
  public onNotificationDismissed = (event: any, notification: AvlNotification): void => {
    if (event) {
      const snackType = (event.target as HTMLElement)?.id?.replace('Snackbar', '');
      this.removeNotification(notification, snackType);
    }
  };

  /** @hidden */
  public openFromStack = (notification: AvlNotification): void => {
    const stackName = this.getStackName(notification.type);
    this[stackName].methodDescription = new SnackbarMethodDescription('hide', true);
  };

  /** @hidden */
  public dismissAll = (): void => {
    this.notificationList = [];
    this.warningStack.activeNotification$.next(undefined);
    this.infoStack.activeNotification$.next(undefined);
  };

  private handleNotification = (notification: any): void => {
    this.previousNotification = this.notificationList[this.notificationList.length - 1];
    this.notificationList.push(notification);
    this.updateStackActiveNotification(notification);
    this.setStackAnimation(notification.type);
  };

  private updateStackActiveNotification = (notification: AvlNotification): void => {
    const stackName: string = this.getStack(notification?.type);
    this.updateStackNotificationList(notification.type);
    this.activeNotification = notification;

    if (this[stackName].notificationList.length > 1) {
      this.reopen = true;
      if (this.previousNotification?.type === notification.type) {
        this[stackName].snackbarAnimation = PositionAnimation.ToStack;

        // silently dismiss notification after previous notification animation
        const timeout = setTimeout(() => {
          this.silentlyDismissNotification(stackName);
          this[stackName].snackbarAnimation = null;
          clearTimeout(timeout);
        }, ANIMATION_DURATION);
      } else {
        this.silentlyDismissNotification(stackName);
      }
    } else {
      this[stackName].activeNotification$.next(notification);
    }
  };
  private updateStackNotificationList = (
    notificationType: AvlNotificationType
  ): AvlNotification[] => {
    const stack = this[this.getStack(notificationType)];
    if (stack) {
      stack.notificationList = this.notificationList.filter(item => item.type === notificationType);
    }
    return stack.notificationList;
  };

  private silentlyDismissNotification = (stackName: string): void => {
    this[stackName].dismissedSilently = true;
    this[stackName].methodDescription = new SnackbarMethodDescription('hide', false);
  };

  private setStackAnimation = (
    notificationType: AvlNotificationType,
    animation?: PositionAnimation
  ): void => {
    const currentStack = this.getStack(notificationType);
    const previousStack = this.getStack(notificationType, true);

    if (
      this[previousStack]?.notificationList?.length > 0 &&
      this.previousNotification?.type !== notificationType
    ) {
      this[previousStack].stackAnimation = animation || PositionAnimation.ToBottom;

      this[currentStack].stackAnimation =
        animation === PositionAnimation.ToTop
          ? PositionAnimation.ToBottom
          : PositionAnimation.ToTop;
    }
  };

  private removeNotification = (notification: AvlNotification, snackType?: string): void => {
    const stackName: string = this.getStack(notification?.type);
    if (this[stackName].dismissedSilently) {
      //notification dismissed silently
      const stackName: string = this.getStack(this.activeNotification?.type);
      this[stackName].activeNotification$.next(this.activeNotification);
      this.reopen = false;
    } else {
      //notification dismissed
      this.notificationList = this.notificationList.filter(
        currentNotification => currentNotification.id !== notification.id
      );
      this.reopenLastNotification(notification, stackName);
    }
  };

  private reopenLastNotification = (notification: AvlNotification, stackName: string): void => {
    let stackNotificationList: AvlNotification[] = this[stackName]?.notificationList;
    if (
      this.isLastNotificationDismissed(notification, this[stackName]) &&
      stackNotificationList?.length > 0
    ) {
      this[stackName].reopen = true;

      stackNotificationList = this.updateStackNotificationList(notification.type);
      const lastNotification = stackNotificationList[stackNotificationList.length - 1];
      if (lastNotification) {
        this[stackName].activeNotification$.next(lastNotification);
      } else {
        this[stackName].stackIndicator = false;
        this[stackName].reopen = false;
        this[stackName].animateStack = PositionAnimation.ToBottom;
        this.setStackAnimation(notification.type, PositionAnimation.ToTop);
      }
    }
  };

  private isLastNotificationDismissed = (
    notification: AvlNotification,
    stack: SnackbarStack
  ): boolean => {
    const lastNotification: AvlNotification =
      stack.notificationList[stack.notificationList.length - 1];
    return lastNotification?.id === notification?.id;
  };
  private getStack(notificationType: number, previous?: boolean): string {
    let stackType: AvlNotificationType;
    if (
      notificationType === AvlNotificationType.Warning ||
      notificationType === AvlNotificationType.Error
    ) {
      stackType = previous ? AvlNotificationType.Info : AvlNotificationType.Warning;
    } else {
      stackType = previous ? AvlNotificationType.Warning : AvlNotificationType.Info;
    }
    return this.getStackName(stackType);
  }

  private getStackName = (stackType: AvlNotificationType): string => {
    return stackType === 1 ? 'warningStack' : 'infoStack';
  };
}
